.main-nav {
  position: relative;
}
@media (min-width: 1280px) {
  .main-nav {
    text-align: right;
    background: transparent;
  }
}
.main-nav__toggle {
  position: absolute;
  top: -42px;
  right: 20px;
  width: 24px;
  height: 16px;
  background-color: transparent;
  cursor: pointer;
  border: none;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 7px;
  right: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 -8px 0 0 #000000;
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav--nojs .main-nav__wrapper {
  position: static;
}
@media (max-width: 767px) {
  .main-nav--nojs .main-nav__list {
    display: block;
  }
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 7px;
  right: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav__wrapper {
  z-index: 2;
  background-color: #ffffff;
  width: 100%;
}
@media (max-width: 767px) {
  .main-nav__wrapper {
    position: absolute;
  }
}
@media (min-width: 1280px) {
  .main-nav__wrapper {
    background: transparent;
  }
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: flex;
    gap: 35px;
  }
}
@media (min-width: 1280px) {
  .main-nav__list {
    flex-wrap: wrap;
  }
}
@media (min-width: 768px) {
  .main-nav__site-list {
    flex-wrap: wrap;
    width: 440px;
  }
}
@media (min-width: 1280px) {
  .main-nav__site-list {
    width: 480px;
  }
}
.main-nav__item {
  padding-top: 22px;
  padding-bottom: 24px;
  text-align: center;
  border-top: 1px solid #e6e6e6;
}
@media (min-width: 768px) {
  .main-nav__item {
    padding: 0;
    border: none;
  }
}
@media (min-width: 768px) {
  .main-nav__item--current {
    box-shadow: 0 8px 0 0 #ffffff, 0 10px 0 0 #68b738;
  }
}
@media (min-width: 1280px) {
  .main-nav__item--current {
    box-shadow: 0 5px 0 0 #ffffff, 0 7px 0 0 #68b738;
  }
}
@media (min-width: 1280px) {
  .main-nav__item--current-desktop {
    box-shadow: 0 5px 0 0 #64a73b, 0 7px 0 0 #ffffff;
  }
}
.main-nav__item a {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 20px;
  font-weight: 500;
  line-height: 20px;
  color: #000000;
  display: block;
  text-decoration: none;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .main-nav__item a {
    font-size: 18px;
    font-weight: 400;
    line-height: 24px;
  }
}
@media (min-width: 1280px) {
  .main-nav__item a {
    font-size: 20px;
    line-height: 30px;
    color: #ffffff;
  }
}
.main-nav__item a:hover[href] {
  color: rgba(0, 0, 0, 0.6);
}
.main-nav__item a:focus[href] {
  color: rgba(0, 0, 0, 0.6);
}
.main-nav__item a:active[href] {
  color: rgba(0, 0, 0, 0.3);
}
@media (min-width: 1280px) {
  .main-nav__item-desktop-pseudo:hover a[href] {
    color: rgba(255, 255, 255, 0.6);
  }
  .main-nav__item-desktop-pseudo:focus a[href] {
    color: rgba(255, 255, 255, 0.6);
  }
  .main-nav__item-desktop-pseudo:active a[href] {
    color: rgba(255, 255, 255, 0.3);
  }
}
@media (min-width: 1280px) {
  .main-nav__item-inner a {
    color: #000000;
  }
}
